
*, *::before, *::after {
  box-sizing: border-box;
}
.frame-wrapper {
  margin: 0 auto;
  position: relative;
}
.frame-wrapper p.notice {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    color: #fff;
    font-size: 14px;
    margin: 0 0;
    background: rgba(0, 0, 0, 0.5);
    background-image: url("../assets/notification.png");
    background-size: contain;
    padding: 0 15px;
    text-align: center;
    z-index: 999;
    background-repeat: repeat-x;
}
.frame-wrapper p.notice.invisible {
      display: none;
}
.frame-wrapper p.notice span {
      display: block;
}
.frame-wrapper p.notice span:first-child {
      font-weight: bold;
      margin-bottom: 15px;
}
.frame {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  background-color: black;
}



