
.tile {
  border: 1px solid #111;
  z-index: 2;
}
.tile.empty {
    box-shadow: rgba(0, 0, 0, 0.5) 0px 5px 10px inset;
    background-color: white;
}
.tile.empty:hover {
      border-color: #111;
      cursor: default;
}
.tile.locked {
    opacity: 0.5;
}
.tile:hover {
    border-color: #f00;
    cursor: pointer;
}



